
"use client";

import * as React from "react";
import { AppLayout } from "@/components/layout/app-layout";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Badge } from "@/components/ui/badge";
import { format, parseISO, isValid, differenceInHours, differenceInMinutes } from 'date-fns';
import type { AttendanceRecord } from "@/types/attendance";
import { useToast } from "@/hooks/use-toast";
import { fetchAllAttendance } from "@/lib/mock-data"; // Import mock function
import { Skeleton } from "@/components/ui/skeleton"; // Import Skeleton

// Helper to format HH:MM time or return '-'
const formatTime = (timeString: string | null | undefined): string => {
  if (!timeString) return "-";
  // Basic check if it resembles HH:MM
  if (/^\d{2}:\d{2}$/.test(timeString)) {
    return timeString;
  }
  // Try parsing if it's a full datetime string (though less likely for HH:MM)
  try {
     const date = parseISO(timeString);
     if (isValid(date)) {
         return format(date, 'HH:mm');
     }
  } catch (e) {
     // Ignore parsing errors if it's just HH:MM
  }
  return "-"; // Fallback
};


// Helper to calculate duration
const calculateDuration = (inTime: string | null, outTime: string | null): string => {
  if (!inTime || !outTime) return "-";
  try {
    // Assume times are on the same day for calculation purposes
    const inDate = parse(`1970-01-01T${inTime}:00`, "yyyy-MM-dd'T'HH:mm:ss", new Date());
    const outDate = parse(`1970-01-01T${outTime}:00`, "yyyy-MM-dd'T'HH:mm:ss", new Date());

    if (!isValid(inDate) || !isValid(outDate) || outDate <= inDate) {
      return "-";
    }

    const hours = differenceInHours(outDate, inDate);
    const minutes = differenceInMinutes(outDate, inDate) % 60;
    return `${hours}h ${minutes}m`;
  } catch (e) {
    console.error("Error calculating duration:", e);
    return "-";
  }
};


// Helper to get status badge
const getStatusBadge = (inTime: string | null, outTime: string | null): React.ReactNode => {
   if (inTime && outTime) {
       return <Badge variant="default">Present</Badge>; // Green for Present (assuming default is green/primary)
   } else if (inTime && !outTime) {
       return <Badge variant="outline">In Office</Badge>; // Yellow/Outline for In Office
   } else {
       return <Badge variant="destructive">Absent</Badge>; // Red for Absent
   }
};

export default function AdminAttendancePage() {
  const [allAttendance, setAllAttendance] = React.useState<AttendanceRecord[]>([]);
  const [filteredAttendance, setFilteredAttendance] = React.useState<AttendanceRecord[]>([]);
  const [employees, setEmployees] = React.useState<string[]>([]);
  const [selectedEmployee, setSelectedEmployee] = React.useState<string>("all"); // Default to show all
  const [isLoading, setIsLoading] = React.useState(true);
  const { toast } = useToast();
  const userRole = "admin";


  React.useEffect(() => {
    const loadData = async () => {
      setIsLoading(true);
      try {
        const data = await fetchAllAttendance();
        // Sort by date descending, then by employeeId
         data.sort((a, b) => {
           const dateComparison = b.date.localeCompare(a.date);
           if (dateComparison !== 0) return dateComparison;
           return a.employeeId.localeCompare(b.employeeId);
         });
        setAllAttendance(data);
        setFilteredAttendance(data); // Initially show all

        // Extract unique employee IDs
        const uniqueEmployees = [...new Set(data.map(record => record.employeeId))].sort();
        setEmployees(uniqueEmployees);

      } catch (error) {
        console.error("Failed to load attendance data:", error);
        toast({ variant: "destructive", title: "Error", description: "Could not load attendance records." });
      } finally {
        setIsLoading(false);
      }
    };
    loadData();
  }, [toast]);

  // Effect to filter attendance when selectedEmployee changes
  React.useEffect(() => {
    if (selectedEmployee === "all") {
      setFilteredAttendance(allAttendance);
    } else {
      setFilteredAttendance(allAttendance.filter(record => record.employeeId === selectedEmployee));
    }
  }, [selectedEmployee, allAttendance]);

  return (
    <AppLayout userRole={userRole}>
      <h1 className="text-2xl font-semibold text-primary mb-6">Employee Attendance Records</h1>

      <Card>
        <CardHeader>
          <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
             <div>
                 <CardTitle>Attendance Log</CardTitle>
                 <CardDescription>View attendance records for all employees.</CardDescription>
            </div>
            {/* Employee Filter Dropdown */}
            <div className="w-full sm:w-auto">
                <Select value={selectedEmployee} onValueChange={setSelectedEmployee} disabled={isLoading}>
                <SelectTrigger className="w-full sm:w-[200px]">
                    <SelectValue placeholder="Filter by Employee" />
                </SelectTrigger>
                <SelectContent>
                    <SelectItem value="all">All Employees</SelectItem>
                    {employees.map(emp => (
                    <SelectItem key={emp} value={emp}>{emp}</SelectItem>
                    ))}
                </SelectContent>
                </Select>
             </div>
          </div>
        </CardHeader>
        <CardContent>
          {isLoading ? (
             <div className="space-y-2">
                <Skeleton className="h-10 w-full" />
                <Skeleton className="h-10 w-full" />
                <Skeleton className="h-10 w-full" />
                <Skeleton className="h-10 w-full" />
             </div>
          ) : filteredAttendance.length === 0 ? (
            <p className="text-muted-foreground text-center py-4">
              No attendance records found {selectedEmployee !== 'all' ? `for ${selectedEmployee}` : ''}.
            </p>
          ) : (
            <div className="overflow-x-auto">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Employee</TableHead>
                    <TableHead>Date</TableHead>
                    <TableHead className="text-center">In Time</TableHead>
                    <TableHead className="text-center">Out Time</TableHead>
                    <TableHead className="text-center">Duration</TableHead>
                    <TableHead className="text-center">Status</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {filteredAttendance.map((record) => (
                    <TableRow key={record.id}>
                      <TableCell className="font-medium">{record.employeeId}</TableCell>
                       <TableCell>{format(parseISO(record.date), 'PPP')}</TableCell> {/* Format date nicely */}
                       <TableCell className="text-center">{formatTime(record.inTime)}</TableCell>
                       <TableCell className="text-center">{formatTime(record.outTime)}</TableCell>
                       <TableCell className="text-center">{calculateDuration(record.inTime, record.outTime)}</TableCell>
                       <TableCell className="text-center">{getStatusBadge(record.inTime, record.outTime)}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
          )}
        </CardContent>
      </Card>
    </AppLayout>
  );
}
